/** necessary for mouse event coords to work right, WTF */
body {
    margin: 0
}

.container {
    display: grid;
    grid-template-columns: 1fr 300px; /* "1fr" means the left column should take up the remaining space */
}

.main {
    grid-column: 1 / 2; /* This places the main div in the left column */
    height: 100vh;
}

.sidebar {
    grid-column: 2 / 3; /* This places the sidebar div in the right column */
    background-color: #dddddd;
    height: 100vh;
    /*overflow:auto;*/
    overflow-x: scroll;
    overflow-y: scroll;
}
.sidebar::-webkit-scrollbar {
    display: none;
}
.tool {
    padding: 1em;
}
.fileDropArea {
    width: 80%;
    height:4em;
    border: 1px solid black;
}


.svgContainer {
    position: relative;
    opacity: 100%;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.svgFront {
    position: absolute;
    opacity: 90%;
    width: 100%;
    height: 100%;
    z-index: 1;
    overflow: hidden;
}

.orig-svg-container {
    position: absolute;
    opacity: 30%;
    width: 100%;
    height: 100%;
    z-index: -9;
    filter: blur(2px) ;
    overflow: hidden;
    /*visibility:hidden;*/
}

