
.badge {
    position:absolute;
    z-index:1;
    top: 0;
    right:.5em;
    transform: translate(0, -20%);
}
.badge, .badgeInline {
    padding-left:.2em;
    padding-right:.2em;
    border-radius: 1em;
    border:thin solid black;
    font-weight:bold;
    font-family:sans-serif;
    color:black;
    text-align:center;
    background:red;

}