select {
    margin-left: .5em;
    margin-right: .5em;
    padding-left: .5em;
    padding-right: .5em;
    border-radius: .5em;
    /*border: 0.5px solid black;*/
    font-weight:bold;
    font-family:sans-serif;
    color:black;
    text-align:left;
    white-space:nowrap;
    background: #00000040;
    background-image: linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0) 70%);
}

table.toolIconSet {
    border-collapse: collapse;
    border-spacing:0;
    border:0;
    padding:0;
    margin-top:0;
    margin-bottom:.4em;
}
table.toolIconSet td {
    /*padding: .1em;*/
    height:4em;
    padding: 0;
    position: relative;
    text-align:center;
}
table.toolIconSet tr {
    vertical-align: middle;
}
td.controlCell {
    width:100%;
}
td.labelCell, td.controlCell {
    padding-top:.2em;
    padding-right:.5em;
    padding-bottom:.2em;
    font-weight:bold;
    font-family:sans-serif;
    color:black;
    vertical-align: middle;
    horiz-align: left;
    white-space: nowrap;
}
div.controlCell {
    padding-top:.2em;
    padding-right:.5em;
    padding-bottom:.2em;
    font-weight:bold;
    font-family:sans-serif;
    color:black;
    vertical-align: middle;
    horiz-align: left;
    white-space: nowrap;
}

input[type='checkbox'] {
    appearance:none;
    width: 1em;
    height: 1em;
    padding:0;
    margin:0;
    margin-left:.5em;
    margin-right:.5em;
    border-radius: .25em;
    background: #00008899;
    background-image: linear-gradient(rgba(255,255,255,0), rgba(255,255,255,0.2) 70%);
}
input[type="checkbox"]:checked {
    background: #00008870;
    background-image: linear-gradient(rgba(255,255,255,0), rgba(255,255,255,0.2) 70%);
    position: relative;
}
input[type="checkbox"]:checked::before {
     content: "\2714";
     font-size: 90%;
     color: black;
     position: absolute;
     top: 50%;
     left: 50%;
     transform: translate(-50%, -50%);
 }
input[type='range'] {
    width: 5em;
    height: .5em;
    margin-left:.5em;
    margin-right:.5em;
    -webkit-appearance: none; /* Removes default styling in WebKit browsers */
    border-radius: .25em;
    background: #00000040;
    background-image: linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0) 70%);
}
input[type='range']::-webkit-slider-runnable-track,
input[type='range']::-moz-range-track {
    background:transparent;
}
input[type='range']::-webkit-slider-thumb {
    -webkit-appearance: none;
    border-radius: 50%;
    color:black;
    background: #00008899;
    background-image: linear-gradient(rgba(255,255,255,0), rgba(255,255,255,0.2) 70%);
    width: 1em;
    height: 1em;
}
input[type='range']::-moz-range-thumb {
    border-radius: 50%;
    color:black;
    background: #00008899;
    background-image: linear-gradient(rgba(255,255,255,0), rgba(255,255,255,0.2) 70%);
    width: 1em;
    height: 1em;
}
